body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.watch {
  border: 8px dashed #000;
  border-radius: 50%;
  width: 500px;
  height: 500px;
  display: flex;
  position: relative;
}

.watch__center {
  z-index: 2;
  background-color: #ff0;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  position: absolute;
  top: 237.5px;
  left: 237.5px;
}

.watch__minute-hand {
  z-index: 1;
  background-color: #00f;
  border-radius: 5px;
  width: 15px;
  height: 125px;
  animation: 60s linear move;
  position: absolute;
  top: 121.25px;
  left: 242.5px;
}

.watch__second-hand {
  background-color: green;
  border-radius: 5px;
  width: 4.5px;
  height: 250px;
  animation-name: move;
  animation-duration: 5s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: 12;
  position: absolute;
  top: 0;
  left: 247.75px;
}

@keyframes move {
  from {
    transform-origin: bottom;
    transform: rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}

